import React from "react";
import { Card, CardActions, CardContent, CardMedia, Button, Typography } from '@material-ui/core';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import DeleteIcon from '@material-ui/icons/Delete';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import moment from 'moment';
import { useDispatch } from "react-redux";
import useStyles from "./styles";

import { deletePost, likePost } from "../../../actions/posts";
const Post = ({ post, setCurrentId }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const user = JSON.parse(localStorage.getItem('profile'));

  return ( 
    <Card className={classes.card} raised elevation={10}>
      <CardMedia className={classes.media} image={post.selectedFile} title={post.artist} component="div" />
      <div className={classes.overlay}>
        <Typography variant="h6">{post.name}</Typography>  
        <Typography variant="body2">{moment(post.createdAt).fromNow()}</Typography>  
      </div>
      <div className={classes.overlay2}>
       {(user?.result?.googleId === post?.creater || user?.result?._id === post?.creator) && (
           <Button style={{color: 'white'}} size="small" onClick={() => {setCurrentId(post._id)}}>
            <MoreHorizIcon fontSize="medium" />  
            </Button>
        )} 
      </div>
      <div className={classes.details}>
        <Typography variant="body2" color="textSecondary">{post.tags.map((tag) => `#${tag} `)}</Typography>  
      </div>
      <Typography className={classes.title} variant="h5" gutterBottom>{post.album}</Typography>  
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">{post.artist}</Typography>  
      </CardContent>
      <CardActions className={classes.cardActions}>
        <Button size="small" color="primary" onClick={() => dispatch(likePost(post._id))}>
          <ThumbUpAltIcon fontSize="small" />
            &nbsp;
            {`Like ${post.likes.length}`}  
        </Button>
        {(user?.result?.googleId === post?.creater || user?.result?._id === post?.creator) && (
          <Button size="small" color="primary" onClick={() => dispatch(deletePost(post._id))}>
            <DeleteIcon fontSize="small" />
              Delete 
          </Button>
        )}
      </CardActions> 
    </Card>
   );
}
 
export default Post;